/* Set a background with a gradient to make it more appealing */
body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(to right, #2c3e50, #4ca1af); /* A nice gradient */
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  position: relative;
}

/* Center the login form and add styling */
.App-header {
  background-color: white;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
  width: 300px;
}

h1 {
  color: #333; /* Dark grey for contrast */
  margin-bottom: 20px;
}

/* Style the logo */
.company-logo {
  width: 100px;
  margin-bottom: 20px;
}

/* Style input fields */
input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
  font-size: 1rem;
}

/* Style labels */
label {
  display: block;
  text-align: left;
  margin-bottom: 5px;
  font-weight: bold;
  color: #333; /* Dark text for labels */
}

/* Style the button */
button {
  width: 100%;
  padding: 10px;
  background-color: #4ca1af; /* Complementary color to match the gradient */
  border: none;
  border-radius: 5px;
  color: white;
  font-size: 1rem;
  cursor: pointer;
}

button:hover {
  background-color: #3b8d99; /* Slightly darker on hover */
}

/* Style for responsiveness */
@media (max-width: 768px) {
  .App-header {
    width: 90%;
  }
}

/* Style the error message */
p {
  color: red;
  font-weight: bold;
}

/* Style for the welcome message after successful login */
.welcome-message {
  background: #ffffff;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Footer styling */
.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
  background-color: #282c34;
  color: white;
  padding: 10px;
  font-size: 0.9rem;
}

.footer p {
  margin: 0;
}
